body {
  box-sizing: border-box;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul,
li,
ol {
  list-style: none
}




/* 다 감싼 div */
.App {
  width: 100%;
  height: auto;
  /* background-color: pink; */
}



/* 각파트 제목 */
.title {
  font-size: 28px;
  color: #1A1A1A;
  font-weight: bold;
  letter-spacing: -1px;
}
/* 소제목 */
.small_t {
  font-size: 16px;
    color: #1A1A1A;
    letter-spacing: -1px;
    margin-left:20px;
}


/*글 스타일 */
.h3Style {font-size: 14px;
  font-weight: 1000;
  color: #1A1A1A;
  letter-spacing: -1px;
  cursor: pointer;
}

.h4Style{font-size: 13px;
  font-weight: normal;
  color: #555;}

.Pstyle {font-size: 15px;
  font-weight: bold;
  color: #1A1A1A;
  margin-top: 20px;}


/* 푸터 */
.footer {margin:0 auto;  padding:0px 0 120px; }

/* 푸터 row상자 */
.footerWrapBox {justify-content: space-between;}

/* 푸터 왼쪽 상자 */
.left_fot_box { margin-bottom: 30px;}


/* 정보 목록 */
.malbon_ul_left { width:500px; height:50px; padding:0; margin-top:30px;}
.malbon_ul_left > li {float:left; margin-right:7px;  font-size:13px; color:#9a9a9a; }
.malbon_ul_left > li > a:not(.privacy) {color:#9a9a9a; text-decoration: none;}
.privacy {color:#fe4a49; text-decoration: none;}
.privacy:hover {color:#fe4a49;}
.malbon_ul_left > li > a:hover:not(.privacy) {color:#000;}


/* 자세한 정보들 */
.footer_info {width:700px; height:auto; letter-spacing: -0.6px; line-height:1.9; font-size:13px; color: #9a9a9a;}

/* 저작권 */
.copyright {font-size:13px; color:#9a9a9a; margin-top:35px; }

/* 푸터 오른쪽 상자 */
.right_fot_box { display:flex; justify-content:flex-end; }
.malbon_ul_right {padding:0; font-size:13px; color:#9a9a9a; line-height:1.9; }
.malbon_ul_right > li {cursor:pointer; width:100%; margin-left:25px;}
.malbon_ul_right > li:hover {color:#000; }

