
/* 정렬 버튼  */
.btn-rounded {width:110px; font-size:13px; color:#4f4f4f; margin-left:8px; font-weight:bold;  border-radius: 30px;  padding:8px}


/*코디----------------------------- */
.bigBox {border:1px solid #e8e8e8; width:auto; height:auto; }


.spImgBox > img {width:100%;}
.txtBox {width:100%; height:160px;  display:flex; flex-direction:column; justify-content:space-between;  padding:40px 40px 0;}
.name {font-size:19px; font-weight:bold; color:#1a1a1a; }
.desc {font-size:14px; color:#8a8a8a; }
.bg {width:100%; height:20px; background-image:url("../public/img/codi_line_bg.png"); background-position: center;}



.clothesBox {width:100%; height:auto; padding:40px 25px;}
.clothesBox > ul {width:100%; height:auto; padding:0;}
.clothesBox > ul > li {width:100%; height:150px; display:flex; align-items: center;}
.thumbnail {width:35%; height:120px; display:flex; align-items: center;}
.thumbnail > img {width:100%; height:100%; }

.thumbnail {width:35%; height:120px; display:flex; align-items: center;}
.thumbnail > img {width:100%; height:100%; }

.thumbnail {width:35%; height:120px; display:flex; align-items: center;}
.thumbnail > img {width:100%; height:100%; }



.description {width:60%; height:100px; padding-left:10px; display:flex; flex-direction: column; justify-content:center; }
.description > h5 {font-size:13px; color:#1a1a1a; font-weight: bold; letter-spacing: -1px;}
.description > p {font-size:13px; color:#aaaaaa; letter-spacing: -1px;}
.description > span {font-size:12px; font-weight:bold; color:#1a1a1a;  }



/* 베스트 아이템 */

.Bst_imgBox > img {width:100%; height:100%; cursor: pointer;}
.Bst_textBox {width:100%; margin-top:20px;}


/* 상품더보기 버튼 */
.btn-outline-primary {width:140px; }



/* --------------------------------------------------------------------------- */

/* 서브 페이지 css */


.productBox { margin-top:80px;}

.productBox > h5 {font-size:13px; color:#999999; margin-top:20px;}

.pro_info {border-top:2px solid #000;}

.Title_box {padding-top:38px; margin-bottom:20px;}
.Title_box > h2 {font-size:24px; font-weight:bold; color:#1a1a1a;}

.Price_box {margin:10px 0 19px; padding-bottom:43px; border-bottom:1px solid #e8e8e8 ; }
.Price_box > h1 {font-size:28px; font-weight:bold; color:#1a1a1a;}



.Version_box { margin-bottom:20px;}
.Version_box > h3 {font-size:14px; color:#353535; margin-bottom:8px;}

.Version_box > ul {width:200px; display:flex; justify-content: space-between; padding:0; }
.thumb {width:60px; height:60px; border:1px solid #ededed; cursor:pointer;}
.thumb > img {width:100%; height:100%;}



.pro_option {padding:28px 0px; border-top:1px solid #e8e8e8;}
.pro_option > table {width:100%;}
.pro_option > table tr {width:100%;  display:flex; justify-content:space-between; align-items: center; }
.pro_option > table tr th { font-size:14px; font-weight:normal; color:#353535;}
.pro_option > table tr td {width:70%; }



.Btn_Box {margin-top:20px; height:85px; display:flex; justify-content: space-between;}
.Btn_Box > button:first-child {background-color:#005542; width:43%; height:55px; font-weight:bold;}
.Btn_Box > button:first-child:hover {background-color:#00372b; }

.Btn_Box > a {width:27%;}
.Btn_Box > a > button {width:100%; height:55px; font-weight:bold; }



/* ---------------------------------------------------- */

/* 네비2 */
.NavTabs {background-color:#fbf8f8; margin-top:100px; }

.reviewNum {font-size:11px; background-color:#cacaca; color:#fff; font-weight:bold; margin-left:10px; width:16px; height:16px; padding:2px; border-radius:3px;  }

/* ---------------------------------------------------- */

/* 배송/교환/반품 컴포넌트 글 박스 */
.info_box {border: 1px solid #e8e8e8; padding:80px 60px; margin-top:80px;}
.info_box > h3 {font-size:12px; font-weight:700; color:#1a1a1a; margin-bottom:25px;}

.txt_info_box {margin-bottom:90px; color:#8a8a8a; font-size:12px;}

/* ---------------------------------------------------- */

/* 상품후기  */
.reviewBox {margin-top:80px; margin-bottom:80px;}

.review_title {height:70px;}
.review_title > p {font-size:16px; color:#1a1a1a; font-weight: bold;}
.review_title > p > span {font-size:14px; color:#898989; font-weight: normal; }


/* 리뷰칸 */
.review_point {border:1px solid #e6e9ec; height:auto; display:flex; align-items: center; }

/* 중간중간 선 */
.right_line {height:100px; border-right:1px solid #e6e9ec; }

/*리뷰칸 첫번째 */
.pro_Satisfaction {padding:20px; text-align: center; width:25%;  display:flex; flex-direction: column; justify-content: center;}
.pro_Satisfaction > h5 {font-size:13px; }

.pro_Satisfaction > h4 {font-size:24px; font-weight:bold; }
.star {color:orange; }
.grey {color:#b8bbc2;}

.pro_Satisfaction > p {font-size:13px; color:#b8bbc2; }
.pro_Satisfaction > p > span {color:#333336; }


/*리뷰칸 2번째 */
.pro_stats {width:23%; padding:20px 25px; }

.widget_stats {display:flex; justify-content:space-between; height:110px; }

.widget_stats_item {display:flex; flex-direction: column; align-items: center; justify-content: space-between;}
.widget_stats_bar {width:8px; height:60px; background-color:#f6f7f8; position:relative; }
.widget_stats_bar > div {background-color:#333336; width:100%; height:2px; position:absolute; bottom:0; left:0; }

.widget_stats_text_1 {font-size:13px; font-weight:600; }
.widget_stats_text_2 {font-size:13px; color:#b8bbc2;}


/* 리뷰칸 3번째 */
.pro_survey {padding:20px 35px; width:30%;  display:flex; flex-direction: column; }

.pro_survey > h2 {font-size:13px; }
.surveyBox {width:100%; height:auto; }

.overFlowBox { height:95px; overflow-y:scroll; -ms-overflow-style: none;}
.overFlowBox::-webkit-scrollbar { 
    width: 0 !important;
    display: none; 
}

.survey_choice {position:relative; width:280px; height:30px; font-size:13px; padding:0 10px;  margin-bottom:5px; display:flex; align-items: center; background-color:#f6f7f8; color:#333336; border-radius: 5px;}

.survey_select1{width:40%; padding:0 8px;  height:100%; display:flex; align-items: center; background-color:#f6f7f8; color:#333336;  height:100%; border-radius: 5px; position:absolute; top:0; left:0; background-color:#ffa34771;}
.survey_select2 {width:40%; padding:0 8px; height:100%; display:flex; align-items: center; background-color:#f6f7f8; color:#333336;  height:100%; border-radius: 5px; position:absolute; top:0; left:0; background-color:#ffa34742;}
.survey_select3 {width:25%; padding:0 8px; height:100%; display:flex; align-items: center; background-color:#f6f7f8; color:#333336;  height:100%; border-radius: 5px; position:absolute; top:0; left:0; background-color:#ffa34726;}
.survey_select4 {width:25%; padding:0 8px; height:100%; display:flex; align-items: center; background-color:#f6f7f8; color:#333336;  height:100%; border-radius: 5px; position:absolute; top:0; left:0; background-color:#ffa34720;}


/* 리뷰 4번째 */
.pro_share {padding:20px 25px; width:26%; display:flex; flex-direction: column; align-items: center;}
.pro_share > h3 {font-size:13px; letter-spacing: -1px;}
.pro_share > .reviewBtn {width:120px; font-size:13px; cursor:pointer;  display:flex; align-items: center; justify-content: center; height:35px; border-radius: 6px; border:1px solid #e6e9ec; color:#91949c;}



/* ------------------------------------------------------- */

/* 포토리뷰 제목 */
.photo_review {margin-top:30px; }
.photo_review > span:first-child {font-size:13px; font-weight:bold;  margin-right:5px;}
.photo_review > span:last-child {font-size:10px; color:#b0b0b0; }


.photo_reviewBox {border:1px solid #e5e5e5; width:310px; height:420px; background-color:#f6f7f8;}
.photo_Box {width:100%; height:70%;}
.photo_Box > img {width:100%; height:100%;}


.photo_Text {width:100%; height:30%; background-color:white; padding:14px 16px; }
.starBox {color:orange; font-weight:bold; margin-bottom:5px;}
.starBox > span {color:#91949c; font-weight: normal; font-size:13px; margin-left:7px;}

.photo_review_TextBox {width:100%; }
.photo_review_TextBox > h5 {font-size:13px; line-height: 1.4;}
.photo_review_TextBox > p {font-size:13px; color:#91949c; }
.photo_review_TextBox > p > span:last-child {margin-left:20px;}


