/* 댓글달기 */


.Comment_input_box { display:flex; border-top:solid 1px #eaeaea; border-bottom:solid 1px #eaeaea; flex-direction: column; align-items: flex-start; justify-content:space-evenly;  height:450px; }

/* 결과 나오는 댓글 상자 */
.commentBox {border-bottom:solid 1px #eaeaea; padding:28px 0; display:flex; }

